import { motion, useAnimation } from "framer-motion";
import { useEffect, useState } from "react";

export default function Home() {
  const fullText = " ,    UX UI ";
  const [displayText, setDisplayText] = useState("");
  const [showCursor, setShowCursor] = useState(true);

  useEffect(() => {
    let i = 0;
    const interval = setInterval(() => {
      setDisplayText(fullText.slice(0, i));
      i++;
      if (i > fullText.length) {
        clearInterval(interval);
        setShowCursor(false);
      }
    }, 80);
    return () => clearInterval(interval);
  }, []);

  //  controls 
  const controlsJ = useAnimation();
  const controlsY = useAnimation();
  const controlsPhoto = useAnimation();

  useEffect(() => {
    //       floating 
    const startFloating = async () => {
      // J 
      await controlsJ.start({
        opacity: 1,
        scale: 1,
        y: 0,
        rotate: 0,
        filter: "brightness(1.0)",
        transition: { duration: 1.5, ease: "easeOut" },
      });

      // J  
      controlsJ.start({
        y: [0, -8, 5, -3, 7, 0, -4, 3, 0],
        rotate: [0, 4, -6, 3, -2, 5, 0, -3, 0],
        transition: { duration: 6, ease: "easeInOut", repeat: Infinity },
      });

      // Y 
      await controlsY.start({
        opacity: 1,
        scale: 1,
        y: 0,
        rotate: 0,
        filter: "brightness(1.0)",
        transition: { duration: 1.5, ease: "easeOut" },
      });

      // Y  
      controlsY.start({
        y: [0, 6, -5, 8, -4, 7, 0, -3, 0],
        rotate: [0, -5, 6, -3, 2, -4, 0, 3, 0],
        transition: { duration: 6, ease: "easeInOut", repeat: Infinity },
      });

      //  
      await controlsPhoto.start({
        opacity: 1,
        scale: 1,
        y: 0,
        rotate: -2,
        filter: "brightness(1.0)",
        transition: { duration: 1.8, ease: "easeOut" },
      });

      //   
      controlsPhoto.start({
        rotate: [-2, -1, -3, -1, -2],
        y: [0, -2, 1, -3, 0],
        transition: { duration: 8, ease: "easeInOut", repeat: Infinity },
      });
    };

    startFloating();
  }, [controlsJ, controlsY, controlsPhoto]);

  return (
    <section id="home" className="hero container">
      <div className="hero-wrapper">
        <p className="hero-text">
          {displayText.split("UX UI ")[0]}
          {displayText.includes("UX UI ") && (
            <span className="highlight">UX UI </span>
          )}
          {showCursor && <span className="cursor">|</span>}
        </p>

        <div className="hero-main">
          {/*  J */}
          <motion.img
            src={process.env.PUBLIC_URL + "/imgs/homeJ.png"}
            alt="J"
            className="hero-j"
            initial={{
              opacity: 0,
              scale: 0.5,
              y: 50,
              rotate: -5,
              filter: "brightness(1.3)",
            }}
            animate={controlsJ}
          />

          {/*   */}
          <motion.div
            className="hero-photo"
            initial={{
              opacity: 0,
              scale: 0.7,
              y: 50,
              filter: "brightness(1.3)",
            }}
            animate={controlsPhoto}
          >
            <img
              src={process.env.PUBLIC_URL + "/imgs/homeMain.png"}
              alt=" "
              className="profile-bg"
            />
            <img
              src={process.env.PUBLIC_URL + "/imgs/jyprofileG.png"}
              alt=" "
              className="jyphoto"
            />
            <p className="name"></p>
          </motion.div>

          {/*  Y */}
          <motion.img
            src={process.env.PUBLIC_URL + "/imgs/homeY.png"}
            alt="Y"
            className="hero-y"
            initial={{
              opacity: 0,
              scale: 0.5,
              y: 50,
              rotate: 5,
              filter: "brightness(1.3)",
            }}
            animate={controlsY}
          />
        </div>
      </div>
    </section>
  );
}
