import { useState } from "react";

function DeviceDisplay({ work }) {
  const { id, img, site, screenImg, title } = work;

  if (id === 5) {
    //      
    const deviceScreens = {
      ipad: process.env.PUBLIC_URL + "/imgs/littlefarmer-ipad.png",
      laptop: process.env.PUBLIC_URL + "/imgs/littlefarmer.png",
      iphone: process.env.PUBLIC_URL + "/imgs/littlefarmer-iphone.png",
    };
  
    //         
    const handleDeviceClick = (device, e) => {
      e.preventDefault();
  
      let width, height;
      if (device === "laptop") {
        width = 1920;
        height = 900;
      } else if (device === "ipad") {
        width = 768;
        height = 1024;
      } else if (device === "iphone") {
        width = 375;
        height = 812;
      }
  
      window.open(
        site,
        "_blank",
        `width=${width},height=${height},noopener,noreferrer`
      );
    };
  
    return (
      <div className="multi-device">
        {["ipad", "laptop", "iphone"].map((device) => (
          <div
            key={device}
            className={`${device}-frame`}
            onClick={(e) => handleDeviceClick(device, e)} //    
            style={{ cursor: "pointer" }}
          >
            {/*     */}
            <img
              src={process.env.PUBLIC_URL + `/imgs/${device}.png`}
              alt={device}
              className={device}
            />
            {/*     */}
            <div className={`${device}-screen`}>
              <img
                src={deviceScreens[device]}
                alt={`${title} ${device} `}
                className="screen-content"
              />
            </div>
          </div>
        ))}
      </div>
    );
  }
  

  //   ( )
if (img.includes("iphone")) {
  // Series Up (Vue )   
  const handleMobileOpen = (e) => {
    e.preventDefault();
    if (id === 3) {
      window.open(
        site,
        "_blank",
        "width=430,height=932,noopener,noreferrer" //   
      );
    } else {
      window.open(site, "_blank"); //     
    }
  };

  return (
    <div className="iphone-frame">
      <img src={img} alt="" className="iphone" />
      <div className="iphone-screen">
        <a href={site} target="_blank" rel="noopener noreferrer" onClick={handleMobileOpen}>
          <img
            src={screenImg}
            alt={`${title} `}
            className="screen-content"
          />
        </a>
      </div>
    </div>
  );
}


  //  ()
  return (
    <div className="laptop-frame">
      <img src={img} alt="" className="laptop" />
      <div className="laptop-screen">
        <a href={site} target="_blank" rel="noopener noreferrer">
          <img
            src={screenImg}
            alt={`${title} `}
            className="screen-content"
          />
        </a>
      </div>
    </div>
  );
}


